er;background-position:center}.section__info{margin-top:150px}@media (max-width: 37.5em){.section__info{padding:0 0.5rem}}.section__info h1{font-size:2.5rem;color:#393C41}@media (max-width: 37.5em){.section__info h1{font-size:1.9rem}}.section__info label{margin-top:2rem;color:#393C41}@media (max-width: 37.5em){.section__info label{font-size:0.9rem}}.section__info label a{color:#393C41}.section__info label a:hover{font-weight:500}@media (max-width: 37.5em){.section__info label{font-size:0.9rem}}.section__buttons{margin-bottom:50px;display:flex;justify-content:center}@media (max-width: 37.5em){.section__buttons{flex-direction:column;margin-bottom:140px}}.section__buttons button{margin:0 20px;padding:15px 70px;border:none;border-radius:20px;cursor:pointer;text-transform:uppercase;font-weight:600;font-size:.8rem}@media (max-width: 37.5em){.section__buttons button{margin-top:20px}}.section__buttons--dark{background-color:rgba(0,0,0,0.8);color:#fff}.section__buttons--light{background-color:rgba(255,255,255,0.8);color:#000}

